/* Սկզբնական ձևավորում */
.video-player {
    width: 80%;
    max-width: 640px;
    margin: 20px auto;
    display: block;
    border-radius: 10px;
    box-shadow: 0 0 10px gray;
  }
  
  .video-container h2,
  .video-container h1 {
    color: #333;
  }
  
  /* Մոբայլի համար */
  @media (max-width: 600px) {
    .video-player {
      width: 100%;
      max-width: 100%;
      margin: 10px 0;
    }
  
    h1 {
      font-size: 22px;
    }
  
    h2 {
      font-size: 18px;
    }
  
    .back-button {
      font-size: 14px;
      padding: 8px 16px;
      display: inline-block;
      margin-bottom: 20px;
    }
  }
  