
.video-player {
  width: 100%;
  max-width: 800px;
  height: auto;
  border-radius: 15px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
  margin-bottom: 20px;
}

/* Մոբայլ տարբերակի media query */
@media (max-width: 600px) {
  .video-player {
    width: 100% !important;
    max-width: 100% !important;
    height: auto !important;
  }

  h1, h2, h3 {
    font-size: 16px;
    text-align: center;
  }

  .video-container {
    padding: 10px;
  }
}
